    .about {
        .inner {
            .about-section-info{
                display: grid!important;
                display: -ms-grid!important;
                grid-template-columns: 1fr 1fr 1fr;
                -ms-grid-columns: 1fr 1fr 1fr;
                justify-items: center;

                .desktop-about-image:nth-child(1) {
                    grid-column: inherit;
                    -ms-grid-column: 1;
                    -ms-grid-column-span: 1;
                }

                .about-blurb{ 
                    display: flex;
                    justify-content: center;
                    align-content: center;
                    flex-direction: column;
                    grid-column: inherit;
                    -ms-grid-column: 2;
                    -ms-grid-column-span: 1;
                    margin-top: auto;
                    margin-bottom: auto;  
                    margin-left: 0px;
                    margin-right: 0px;
                }

                .desktop-about-image:nth-child(2) {
                    grid-column: inherit;
                    -ms-grid-column: 3;
                    -ms-grid-column-span: 1;
                }
            }
        }
    }


    .desktop-about-image {
        display: block!important;
    }

    .about-images, .tablet-about-images {
        display: none!important;
    }
}

// Product height automation. May need to be deleted
@media screen and (min-height: 800px) {
    .product-info {
        .inner {
            height: auto;
        }
    }
}

@media screen and (min-width: 520px) {
    .slideshow-section {
        .mobile-slideshow {
            .inner {
                .blurb {
                    font-size: 2.5em!important;
                }
            }
        }
    }
}
